.signup-container {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f2f2f2;
    border-radius: 4px;
    font-family: 'Mulisch' , sans-serif;
  }
  
  h2 {
    text-align: center;
    color: #333;
    margin-bottom: 20px;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  input[type="email"],
  input[type="password"],
  input[type = "text"] {
    width: 95%;
    padding: 8px;
    border: 1.5px solid rgba(255, 97, 0 , 1);
    border-radius: 4px;
    margin-right: 8px;
  }
  
  .btn {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: rgba(255, 97, 0 , 1);
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .btn:hover {
    background-color: rgb(164, 66, 4);
  }
  
  .link {
    color: rgba(255, 97, 0 , 1);
    text-decoration: none;
  }
  
  .link:hover {
    text-decoration: underline;
  }
  